import {Component, ElementRef, EventEmitter, forwardRef, Input, OnInit, Output, ViewEncapsulation} from '@angular/core';
import {
  AdaptRadarAngularCustomEventName,
  AdaptTranslateService,
  KeyValueObject,
  toBoolean,
  toNumber
} from '@bmc-ux/adapt-angular';
import {ControlValueAccessor, NG_VALUE_ACCESSOR} from '@angular/forms';
import {RatingModel} from './rating.model';
import {AdaptRadarGenericDirective, AdaptRadarService} from '@bmc-ux/adapt-radar';

const noop = (): void => {
  return;
};

// TODO: Refactor this if possible
/* eslint-disable-next-line @typescript-eslint/no-explicit-any */
export const ADAPT_RATING_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => AdaptRatingComponent),
  multi: true
};

@Component({
  selector: 'adapt-rating',
  // eslint-disable-next-line @angular-eslint/no-host-metadata-property
  host: {
    '[class.a-rating]': 'true',
    '[class.a-rating_small]': 'size === "small" && !readonly',
    '[class.a-rating_medium]': 'size === "medium" && !readonly',
    '[class.a-rating_large]': 'size === "large" && !readonly',
    '[class.a-rating_readonly]': 'readonly',
    '(mouseleave)': 'reset()'
  },
  templateUrl: './rating.component.html',
  encapsulation: ViewEncapsulation.None,
  providers: [ADAPT_RATING_ACCESSOR]
})

export class AdaptRatingComponent extends AdaptRadarGenericDirective implements OnInit, ControlValueAccessor {
  ratingList: RatingModel[] = [];
  currentRating: number;

  @Input() readonly: boolean;

  // eslint-disable-next-line @angular-eslint/no-output-rename
  @Output('active-rate-changed') readonly activeRateChanged: EventEmitter<number> = new EventEmitter<number>();
  // eslint-disable-next-line @angular-eslint/no-output-rename
  @Output('hover-changed') readonly hoverChanged: EventEmitter<number> = new EventEmitter<number>();
  // eslint-disable-next-line @angular-eslint/no-output-rename
  @Output('leave-changed') readonly leaveChanged: EventEmitter<number> = new EventEmitter<number>();

  @Input()
  get size(): 'extra-small' | 'small' | 'medium' | 'large' {
    return this._size;
  }

  set size(v: 'extra-small' | 'small' | 'medium' | 'large') {
    this._size = v;
  }

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  @Input() get disabled(): any {
    return this._disabled;
  }

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  set disabled(v: any) {
    this._disabled = toBoolean(v);
  }

  @Input()
  get tooltip(): string[] {
    return this._tooltip;
  }

  set tooltip(v: string[]) {
    this._tooltip = v;
  }

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  @Input() get maxValue(): any {
    return this._maxValue;
  }

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  set maxValue(v: any) {
    this._maxValue = toNumber(v);
    this._createArrayList();
  }

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  @Input() get rate(): any {
    return this._rate;
  }

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  set rate(v: any) {
    this._rate = toNumber(v);
    this._onChangeCallback(v);
  }

  private _onTouchedCallback: () => void = noop;
  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  private _onChangeCallback: (_: any) => void = noop;
  private _size: 'extra-small' | 'small' | 'medium' | 'large' = 'extra-small';
  private _disabled: boolean = false;
  private _tooltip: string[];
  private _maxValue: number = 5;
  private _rate: number;

  constructor(private ts: AdaptTranslateService,
              _adaptRadarService: AdaptRadarService,
              _elem: ElementRef<HTMLElement>) {
    super(_adaptRadarService, _elem, AdaptRadarAngularCustomEventName.AdaptRating);

    this.setDefaultTexts(ts.getCurrentLanguage());
  }

  setDefaultTexts(translation: KeyValueObject): void {
    this.tooltip = [
      translation['adapt.rating.like.l1'],
      translation['adapt.rating.like.l2'],
      translation['adapt.rating.like.l3'],
      translation['adapt.rating.like.l4'],
      translation['adapt.rating.like.l5'],
    ];
  }

  writeValue(value: number): void {
    if (this._rate !== value) {
      this._rate = value;
    }

    this._updateValue(value);
  }

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  registerOnChange(fn: any): void {
    this._onChangeCallback = fn;
  }

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  registerOnTouched(fn: any): void {
    this._onTouchedCallback = fn;
  }

  ngOnInit(): void {
    this._createArrayList();
    this._updateState(this.rate);
  }

  observerOnClick(value: number): void {
    this._updateValue(value);
  }

  onEnter(value: number): void {
    this.hoverChanged.emit(value);

    if (this._isNotDisabled()) {
      this._updateState(value);
    }
  }

  reset(): void {
    this.leaveChanged.emit(this.currentRating);
    this._updateState(this.rate);
  }

  private _updateValue(v: number): void {
    const ratingValue = this._getValueInRange(v, this.maxValue, 1);

    if (this._isNotDisabled() && this.rate !== ratingValue) {
      this.rate = ratingValue;
    }
    this._updateState(this.rate);
    this.activeRateChanged.emit(this.rate);
  }

  private _getValueInRange(value: number, max: number, min: number = 0): number {
    return Math.max(Math.min(value, max), min);
  }

  private _isNotDisabled(): boolean {
    return !this.readonly && !this.disabled;
  }

  private _updateState(ratingValue: number): void {
    this.currentRating = ratingValue;
    this.ratingList.map((_, i) => _.fill = this._getFillValue(i));
  }

  private _createArrayList(): void {
    this.ratingList = Array.from({length: this.maxValue}, (_, v) => ({fill: 0, index: v}));
  }

  private _getFillValue(index: number): number {
    const diff = this.currentRating - index;

    if (diff >= 1) {
      return 100;
    }

    if (diff < 1 && diff > 0) {
      return Number.parseInt((diff * 100).toFixed(2), 10);
    }

    return 0;
  }
}
